    selectCustGroupInfo() {
        // 
        let parames = {
            groupId: this.groupId
        }
        this.httpService.selectCustGroupInfo(this.custGroupInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.groupType != '3') {
                    this.groupType = this.code['GroupTypeCreate'];
                } else {
                    this.groupType = this.code['GroupTypeSave'];
                }

                if (data.groupType != '4' && data.groupType != '5' && data.groupType != '6' && data.groupType != '10') {
                    this.areaShow = true;
                } else {
                    this.areaShow = false;
                }

                this.custGroupInfoBean = data;
                // 
                this.region = data.region;
                this.queryParentZoneAll();
                // title
                this.title = `${this.custGroupInfoBean.groupName}`;
                this.groupTypeCheck = _.clone(this.custGroupInfoBean.groupType);//
                if (this.groupTypeCheck == 4) {
                    this.isHangye = true;
                    this.httpService.queryTaskByIndtstryAll({ taskTypeId: data.subGroupType, taskTypeKey: 'indtstryType' }).subscribe(data => {
                        this.firstToinvestTradeOptions = this.commfunc.listToLabelvalue(data.firstTask, 'taskTypeName', 'taskTypeId');
                        this.firstToinvestTradeOptions.unshift({ label: '', value: '' });
                        this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.secondTask, 'taskTypeName', 'taskTypeId');
                        this.secondToinvestTradeOptions.unshift({ label: '', value: '' });
                        this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.thirdTask, 'taskTypeName', 'taskTypeId');
                        this.thirdToinvestTradeOptions.unshift({ label: '', value: '' });
                        this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.fourthTask, 'taskTypeName', 'taskTypeId');
                        this.fourthToinvestTradeOptions.unshift({ label: '', value: '' });
                        data.taskList.forEach(item => {
                            switch (item.level) {
                                case '1': this.firstToinvestTrade = item.taskTypeId; break;
                                case '2': this.secondToinvestTrade = item.taskTypeId; break;
                                case '3': this.thirdToinvestTrade = item.taskTypeId; break;
                                case '4': this.fourthToinvestTrade = item.taskTypeId; break;
                                default: break;
                            }
                        })
                    })
                }
                this.groupStateCheck = _.clone(this.custGroupInfoBean.groupStatus);//
                if (this.groupStateCheck == '4') {
                    this.deleteFlag = false;
                }
                this.contactArr = data.contactList;//
                this.introducerArr = data.introducerList;//
                this.mentorRoleArr = data.mentorList;//
                this.selectFor();//
                this.adminSelect();//          
                if (this.groupStateCheck != '2' && this.groupStateCheck != '4') {
                    this.deleteFlag = false;
                }
                //
                this.custGroupInfoBean.region = data.region;//
                this.parentZoneInfoBean.zoneId = this.custGroupInfoBean.region;//
                this.regionReturn();//                         
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }
    selectGroupAdminInfo() {
        // 
        let parames = {
            groupId: this.groupId,
            roleType: 1,
            statusId: 1
        }
        this.httpService.selectGroupAdminInfo(parames).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.contactArr = data.roleList || [];
                this.inValue = _.clone(this.contactArr);
                let arr = [];
                if (data.roleList) {
                    data.roleList.forEach(item => {
                        arr.push(item.userName);
                    });
                }
                this.contactRoleName = arr.join(';')
                console.log(data.roleList)
                console.log(this.contactRoleName)
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }
