    queryParentZoneAll() {
        let params = {
            zoneId: this.region
        }
        let successFnc = data => {
            data.firstZone.forEach(item => {
                this.regionSHeng.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.secondZone.forEach(item => {
                this.regionCity.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.thirdZone.forEach(item => {
                this.regionArea.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.fourthZone.forEach(item => {
                this.regionStreet.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });


            this.regionSHeng = _.clone(this.regionSHeng)
            this.regionCity = _.clone(this.regionCity)
            this.regionArea = _.clone(this.regionArea)
            this.regionStreet = _.clone(this.regionStreet)
            data.zoneList.forEach(item => {
                switch (item.parentZoneLevel) {
                    case '1': this.regionSHeng1 = item.parentZoneId; this.regionArr[3] = item.parentZoneName; break;
                    case '2': this.cengJi_2 = true; this.regionCity1 = item.parentZoneId; this.regionArr[2] = item.parentZoneName; break;
                    case '3': this.cengJi_3 = true; this.regionArea1 = item.parentZoneId; this.regionArr[1] = item.parentZoneName; break;
                    case '4': this.cengJi_4 = true; this.regionStreet1 = item.parentZoneId; this.regionArr[0] = item.parentZoneName; break;
                    default: break;
                }
            });
            console.log(this.regionArr)
            let arr = [];
            this.regionArr.forEach(element => {
                if (element != '' && arr.length < 2) {
                    arr.push(element)
                }
            });
            let str = arr.reverse().join('');
            if (this.custGroupInfoBean.groupName.indexOf(str) != -1) {
                this.custGroupInfoBean.groupName = this.custGroupInfoBean.groupName.substring(str.length)
            }
        }
        this.commfunc.interfaceComFnc(this.httpService.queryParentZoneAll(params), successFnc, this.msgs)
    }

